/* index.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header */
header {
  background-color: #1a1a1a;
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #333;
  height: 60px;
}

header h1 {
  margin: 0 1rem;
  font-size: 1.2rem;
  flex-grow: 1;
}

.menu-btn {
  background: none;
  border: none;
  color: #ffffff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.5rem;
}

.header-controls {
  display: flex;
  gap: 0.5rem;
}

/* Drawer */
.drawer {
  position: fixed;
  left: 0;
  top: 60px;
  bottom: 0;
  width: 300px;
  background-color: #1a1a1a;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  z-index: 1000;
  border-right: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.drawer.open {
  transform: translateX(0);
}

.drawer-header {
  padding: 1rem;
  border-bottom: 1px solid #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.drawer-content {
  padding: 1rem;
  overflow-y: auto;
  flex-grow: 1;
}

/* Line Editor */
.line-editor {
  background-color: #232323;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.line-header {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  margin-bottom: 0.5rem;
}

.line-header select {
  width: 50px;
}

.line-header input {
  flex-grow: 1;
}

.line-controls {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}

/* Form Controls */
button {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #444;
}

.add-btn {
  background-color: #4CAF50;
}

.add-btn:hover {
  background-color: #45a049;
}

.delete-btn {
  background-color: #f44336;
  padding: 0.25rem 0.5rem;
}

.delete-btn:hover {
  background-color: #da190b;
}

input[type="text"] {
  background-color: #333;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
}

input[type="text"].error {
  border-color: #f44336;
}

select {
  background-color: #333;
  border: 1px solid #444;
  color: #fff;
  padding: 0.5rem;
  border-radius: 4px;
}

input[type="color"] {
  width: 40px;
  height: 30px;
  padding: 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.error-message {
  color: #f44336;
  font-size: 0.8rem;
  margin-top: 0.25rem;
}

/* Plot Container */
.plot-container {
  flex-grow: 1;
  position: relative;
  background-color: #1a1a1a;
}

/* Responsive Design */
@media (max-width: 768px) {
  .drawer {
    width: 100%;
  }

  header h1 {
    font-size: 1rem;
  }

  .line-controls {
    flex-direction: column;
    align-items: stretch;
  }

  .line-controls > * {
    width: 100%;
  }
}